  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--bg-content);
}

.list-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 24px;
  border-bottom: 1px solid var(--border-color);
}

.list-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
}

.list-actions {
  display: flex;
  align-items: center;
  gap: 16px;
}

.list-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px 24px;
}

.articles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
  gap: 12px;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
  color: var(--text-muted);

  p {
    margin-top: 16px;
  }
}

.loading-more,
.no-more {
  display: flex;
  align-items: center;
  justify-content: center;
