<template>
  <div class="feed-view">
    <ArticleList
      :title="feedTitle"
      :feed-id="feedId"
      @select="handleSelectArticle"
    />
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useRoute } from 'vue-router'
import ArticleList from '@/components/ArticleList/ArticleList.vue'
import type { Article } from '@/types'
import { useFeedStore } from '@/stores/feed'
import { useArticleStore } from '@/stores/article'

const route = useRoute()
const feedStore = useFeedStore()
const articleStore = useArticleStore()

const feedId = computed(() => Number(route.params.id))

const feedTitle = computed(() => {
  const feed = feedStore.feeds.find(f => f.id === feedId.value)
  return feed?.feedName || '订阅源'
})

async function handleSelectArticle(article: Article) {
  if (article.isRead === 0) {
    await articleStore.markAsRead(article.id)
  }
}
</script>

<style lang="scss" scoped>
.feed-view {
  height: 100%;
  overflow: hidden;
}
</style>
